.gallery {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

*, :before, :after {
  box-sizing: border-box;
  outline: none;
}

figure {
  width: 395px;
  height: 290px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100%;
  border-radius: 10px;
  justify-content: center;
  align-items: flex-start;
  padding: 20px;
  transition: all .5s cubic-bezier(.25, .8, .25, 1);
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 14px 28px #00000040, 0 10px 10px #00000038;
}

figure:before, figure:after {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

figure:before {
  content: "";
  z-index: 0;
  background-color: #0000001a;
}

figure:after {
  content: "";
  justify-content: center;
  align-items: center;
  transition: all .5s cubic-bezier(.25, .8, .25, 1);
  display: flex;
  overflow: hidden;
}

figure:hover {
  background-size: 175%;
}

figure:hover:after {
  content: "♣︎";
  color: #fff;
  z-index: 2;
  background-color: #00fede;
  font-size: 72px;
}

figure:hover .date {
  bottom: -59px;
}

figure:hover figcaption {
  transform: translateY(-110%);
}

figure figcaption {
  color: #fff;
  z-index: 1;
  transition: all .5s cubic-bezier(.25, .8, .25, 1);
}

figure figcaption h4 {
  text-shadow: 1px 1px #000000b3;
  margin: 0 0 5px;
  font-family: Source Code Pro, sans-serif;
  font-size: 24px;
  line-height: 1.35;
}

figure figcaption h4 > span {
  background-color: #f1c40fcc;
}

figure figcaption p {
  margin: 0;
  line-height: 1.5;
}

figure {
  margin: 0;
}

.photo-card {
  color: #00fede;
  flex-direction: column;
  align-items: center;
  margin: 20px;
  display: flex;
}

.info {
  width: 395px;
  flex-wrap: wrap;
  padding: 20px;
  display: flex;
}

.info-item {
  margin-right: 40px;
}

